
import { defineComponent, toRefs } from "vue";
import { useRouter } from "vue-router";

const Component = defineComponent({
  name: "FabricCard",
  props: {
    data: {
      type: Object,
      default: () => ({
        id: "id",
        main_media: {
          file: ""
        },
        name: "",
        real_composition: "",
        favorite: false
      })
    },
    hasButtons: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const router = useRouter();
    const { data, hasButtons } = toRefs(props);

    const onImageClick = async () => {
      await router.push({
        name: "FabricDetail",
        params: { id: data.value.id }
      });
    };

    const onFavoriteClick = () => {
      if (!hasButtons) return;
      alert("favorite");
    };

    const onAddClick = () => {
      if (!hasButtons) return;
      alert("add");
    };

    return {
      onImageClick,
      onFavoriteClick,
      onAddClick
    };
  }
});

export default Component;
