<template>
  <div id="fabric-card-component">
    <div
      class="cover-image"
      :style="{ backgroundImage: `url('${data.main_media.file + '@250-250'}')` }"
      @click="onImageClick"
    ></div>
    <div class="content-container">
      <div
        class="content-wrapper"
        :style="{ width: hasButtons ? '70%' : 'auto' }"
      >
        <h3 class="title">{{ data.name }}</h3>
        <p class="description">{{ data["real_composition"] }}</p>
      </div>
      <div v-if="hasButtons" class="buttons-group">
        <div class="button-wrapper" @click="onFavoriteClick">
          <font-awesome-icon
            class="icon heart"
            :icon="[`${data.favorite ? 'fas' : 'far'}`, 'heart']"
          ></font-awesome-icon>
          <p class="icon-name">Favorite</p>
        </div>
        <div class="button-wrapper" @click="onAddClick">
          <font-awesome-icon
            class="icon plus"
            :icon="['fas', 'plus-circle']"
          ></font-awesome-icon>
          <p class="icon-name">Add</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs } from "vue";
import { useRouter } from "vue-router";

const Component = defineComponent({
  name: "FabricCard",
  props: {
    data: {
      type: Object,
      default: () => ({
        id: "id",
        main_media: {
          file: ""
        },
        name: "",
        real_composition: "",
        favorite: false
      })
    },
    hasButtons: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const router = useRouter();
    const { data, hasButtons } = toRefs(props);

    const onImageClick = async () => {
      await router.push({
        name: "FabricDetail",
        params: { id: data.value.id }
      });
    };

    const onFavoriteClick = () => {
      if (!hasButtons) return;
      alert("favorite");
    };

    const onAddClick = () => {
      if (!hasButtons) return;
      alert("add");
    };

    return {
      onImageClick,
      onFavoriteClick,
      onAddClick
    };
  }
});

export default Component;
</script>

